MeteorToys_Result = {
	'insert': function (result, type, params) {
		
		// Create the data objet
		resultObject = {
			"result": result,
			"timestamp": new Date(),
		}
		
		if (type) {
			resultObject.type = type;
		}

		if (params) {
			resultObject.params = params;
		}

		// Insert it into the collection
		Package['meteortoys:toykit'].MeteorToysData["Result"].insert(resultObject);

		// Open the Meteor Toy orb
		$("#MeteorToys_result").click();

		// Set the proper position for orb
		MeteorToysDict.set("currentResult", 0);
	}
}